@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
:root{--primary-dark: black;--primary-white: white;--primary-green: #8FA800;--secondary-dark: #333333}html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after{content:'';content:none}q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}.home__main{height:calc(100vh - 120px);background-size:cover;background-position:center 50%;background-repeat:no-repeat;background-clip:border-box;background-attachment:fixed}.outils__main{height:calc(100vh - 120px);background-size:cover;background-position:center -30%;background-repeat:no-repeat;background-clip:border-box;background-attachment:fixed}.navigation{position:relative;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.navigation__left,.navigation__right{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding-top:2.2em}.navigation .nav{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;padding-top:1em}.navigation .nav__item{font-size:24px}.navigation .nav__item:hover{color:#8FA800;color:var(--primary-green);transition:all ease-in-out 200ms}.navigation .nav__expect a:hover{color:#8FA800;color:var(--primary-green);transition:all ease-in-out 200ms}.navigation .nav__logo img{max-width:200px;margin:1em 1em  0em 1em}.navigation .nav__line{width:2px;height:30px;background:#333333;background:var(--secondary-dark);margin:0 1em}.header{width:100%;height:120px;position:relative;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background:black;background:var(--primary-dark);font-family:'Bebas Neue', cursive;color:white;color:var(--primary-white)}.header a{text-decoration:none;color:white;color:var(--primary-white)}.header__line{position:absolute;top:29%;width:100%;height:3px;background-color:#8FA800;background-color:var(--primary-green)}

